body{
  background-color:#1d1b27;
}

.container{
  background-color:#0e0731;
  width: 400px;
  height: 400px;
  margin:auto;
  margin-top: 220px;
  box-shadow: 0px 0px 50px #4f4870;
  border-radius: 10px;
}

.login-button{
  width: auto;
}

h1, form, input, h3{
  text-align: center;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  padding: 10px;
  font-size:2.5em;
  color:#b3acd6;
}
input{
  font-size:0.5em;
  background-color:#0e0731;
  margin:0 0 0 57px;
  border-radius: 100px;
  border: 0.25px #322864 solid;
  box-shadow: 0px 0px 60px #180d50;
  color:#000000;
  margin: auto auto auto auto;
  font-family: 'Noto Sans Mono', monospace;
  transition: .25s;
}

button{
  background-color:#2d1d7a;
  color:#ffffff;
  width: 35px;
  height: 35px;
  border-radius: 100px;
  text-align: center;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  transition: .5s;
}

button:hover, input:hover, input:focus, .todo-list-button:hover,.todo-list-item-button:hover{
  background-color:#f2f2f2;
  color:#000000;
  box-shadow: 0px 0px 60px #180d50;
}

.page-container{
  background-color: white;
  width: 400px;
  margin: auto;
  margin-top: 100px;
  padding: 5px;

  border-radius: 10px;
}

.todo-list{
  display: flex;
  text-align: left;
  padding: 0px;
  margin:0;
  color: #000000;
}

h3.todo-list{
  font-size:20px;
}

.todo-list-input{
  text-align: left;
  position:relative;
  background-color:#ffffff;
  border-radius: 6px 0px 0px 6px;
  border:  0.25px #322864 solid;
  border-right: none;
  box-shadow: 0px 0px 0px #180d50;
  margin: 0;
  z-index: 999;
}

.todo-list-input::placeholder{
  color: black;
}

.todo-list-input:hover, .todo-list-input:focus, .todo-list-button:hover,.todo-list-item-button:hover,.todo-list-button:focus{
  box-shadow: 0px 0px 10px #0e0731;
}

.todo-list-button{
  background-color:#ccc7e4;
  width: 49px;
  height: 49px;
  padding: 5px;
  border-radius: 0px 6px 6px 0px;
  box-shadow: none;
  border-left: none;
  margin: 0;
  z-index: 999;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 49px;
  background-image: url('https://static.vecteezy.com/system/resources/previews/000/582/474/large_2x/plus-icon-vector-illustration.jpg');
}

.todo-list-items{
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: auto;
  height: auto;
  padding-inline-start:0;
  font-family: 'M PLUS Rounded 1c', sans-serif;
}

.todo-list-item{
  background-color: #f2f2f2;
  display: flex;
  justify-content: space-between;
  border-radius: 6px 0px 0px 6px;
  border:  none;
  width: auto;
  height: auto;
  margin:3px;
  border-radius: 6px 0px 0px 6px;
}

.todo-list-item>h2{
  margin: 0;
  margin-right: 10px;
}


.todo-list-item-button{
  background-repeat: no-repeat;
  background-position: center;
  background-color:transparent;
  color: black;
  position: sticky;
  border-radius:10px;
  box-shadow: none;
  border-width:0px;
}

.del-div{
  position: sticky;
  display: flex;
  flex-direction: row-reverse;
  width: auto;
}


.del-btn{
  width: auto;
  margin: 10px;
  align-items:right;
}


body::-webkit-scrollbar {
  width:10px;
}

body::-webkit-scrollbar-track {
  background-color: #1d1b27;
}

body::-webkit-scrollbar-thumb {
  margin:auto;
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius:10px;
}